import React, { useState } from "react";
import { Container, Col, Card, Row, Form, Button } from 'react-bootstrap';
import ToDoLists from "./ToDoLists";

const MainComp = () => {
    const [inputList, setinputList] = useState("");
    const [Items, setItems] = useState([]);

    const itemEvent = (event) => {
        setinputList(event.target.value);
    };
    const listOfItems = () => {
        setItems((oldItems) => {
            return [...oldItems, inputList];
        });
        setinputList("");
    };
    
    const deleteItems = (id)=>{
        console.log('Item deleted')
        setItems((oldItems) => {
            return oldItems.filter((arrElement, index)=>{
                return index !== id;
            });
        });
    }
    return (
        <>
            <Container className='justify-content-center mt-4'>
                <Row className='mx-auto'>
                    <Col className='mx-auto' xs={12} md={8}>
                        <Card border="primary" className='mx-auto' style={{ width: '18rem' }}>
                            <Card.Header>ToDo List</Card.Header>
                            <Card.Body>
                                <Card.Title className='d-flex'>
                                    <Form.Control className='m-1' type="text" placeholder="Add ToDo"  onChange={itemEvent} />
                                    <Button onClick={listOfItems} className='m-1' variant="primary" size="sm">
                                        +
                                    </Button>
                                </Card.Title>
                                    {Items.map((itemval,id) => {
                                      return <ToDoLists 
                                                key={id} 
                                                id={id} 
                                                text={itemval}
                                                onSelect={deleteItems}
                                            />
                                    })}
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default MainComp;