import React from "react";
import { Container } from 'react-bootstrap';


const HeadComp = () => { 
        const userName = prompt("Enter Your Name");
    return (
        <>
            <Container className='justify-content-center mt-4 text-white text-center'>
                <h1>Hello, {userName} </h1>
                <p>What you'r going <strong>ToDo</strong> today?</p>
            </Container>
        </>
    )
}

export default HeadComp;